import {
    Component,
    OnInit,
    OnDestroy,
    ViewChild,
    ElementRef,
} from "@angular/core";
import { Site, SitesService, AssetType } from "../../../api";
import { DashboardService } from "../../services/dashboard.service";
import { Subscription } from "rxjs/Subscription";
import { Router } from "@angular/router";
import { StoreService } from "../../../shared/services/store.service";
import { ModalComponent } from "../../../shared/components/modal/modal.component";
import { Subject } from "rxjs/Subject";
import { ErrorsService } from "../../../global/services/errors.service";
import { SiteToolbarComponent } from "../../components/site-toolbar/site-toolbar.component";
import { ScriptService } from "../../../shared/services/script.service";
import {
    FormBuilder,
    FormGroup,
    FormControl,
    Validators,
    ValidationErrors,
} from "@angular/forms";

import { GetMainDomainPipe } from "../../../shared/pipes/get-main-domain.pipe";

/**
 * Page d'accueil du dashboard
 * affiche un menu et liste les sites de l'user (+ crea et delete site)
 */
@Component({
    selector: "dashboard-accueil",
    templateUrl: "./accueil.component.html",
    styleUrls: ["./accueil.component.scss"],
})
export class AccueilComponent implements OnInit, OnDestroy {
    @ViewChild("modalDomain")
    modalDomain: ModalComponent;
    @ViewChild("modalSite")
    modalSite: ModalComponent;
    @ViewChild("modalMedia")
    modalMedia: ModalComponent;
    @ViewChild("modalDeleteSite")
    modalDeleteSite: ModalComponent;
    @ViewChild("siteToolbar")
    siteToolbar: SiteToolbarComponent;
    @ViewChild("nameSiteInput") nameSiteInputRef: ElementRef;

    // determine l'ordre du listing des sites
    listConf = {
        order: "",
    };

    // Subscription de l'observable d'event du dashboard
    subscription: Subscription;
    canDeleteSite: boolean = false;

    state: any = {};

    actions = {
        // edit_site: $event => this.editSite($event.params),
        delete_site: ($event) => this.wantDeleteSite($event.params.site),
        offline_site: ($event) => this.wantMaintenanceSite($event.params.site),
        register_domain_name: () => this.modalDomain.open(),
    };

    pendingDelete = false;

    constructor(
        private siteApi: SitesService,
        public dashBService: DashboardService,
        public storeService: StoreService,
        private scriptService: ScriptService,
        private getMainDomain: GetMainDomainPipe
    ) {
        this.subscription = this.dashBService.events.subscribe(
            ($e) => this.actions[$e.action] && this.actions[$e.action]($e)
        );
    }

    ngOnInit() {
        this.scriptService.loader("UtilsRcpt").then(() => {
            this.dashBService.getSites();
        });
    }

    // met a jour le filtre du listing de sites
    refreshList(filter: Filter) {
        this.listConf.order = filter.by;
    }

    onCreateNew(e) {
        this.dashBService.showSiteTemplates = e;
    }

    wantMaintenanceSite(site: Site) {
        const status = !site.online;
        this.siteApi.apiSitesSlugPatchPut({online: status}, site.id.toString()).subscribe(e=>{
            location.reload();
        })
        
        //alert("Site status :"+ status);
    }

    wantDeleteSite(site: Site) {
        this.state.site = site;
        this.state.domain_name = this.getMainDomain.transform(
            site.domains,
            "name"
        );
        this.modalDeleteSite.open();
    }

    // supprime un site via l'api
    deleteSite() {
        if (this.pendingDelete) {
            return;
        }
        this.pendingDelete = true;
        this.dashBService.limitationSite = null;
        this.dashBService.deleteSite(this.state.site).then(() => {
            this.modalDeleteSite.close();
            this.pendingDelete = false;
        });
    }

    ngOnDestroy() {
        // prevent memory leak when component destroyed
        this.subscription.unsubscribe();
        this.state = null;
    }

    onHideModalDelete() {
        this.state.site = null;
        this.nameSiteInputRef.nativeElement.value = "";
        this.canDeleteSite = false;
    }

    checkStateBtnDelete($event) {
        this.canDeleteSite =
            this.state.site &&
            this.nameSiteInputRef &&
            this.state.domain_name ===
                this.nameSiteInputRef.nativeElement.value;
    }
    
}



