import {
    Component,
    OnInit,
    EventEmitter,
    Output,
    HostListener
} from "@angular/core";
import {
    SitesService,
    MenuType,
    MenusService,
    Site,
    PublicService
} from "../../../api";
import { DashboardService } from "../../services/dashboard.service";
import { environment } from "../../../../environments/environment";
import { ActivatedRoute } from "@angular/router";

@Component({
    selector: "dashboard-site-toolbar",
    templateUrl: "./site-toolbar.component.html",
    styleUrls: ["./site-toolbar.component.scss"]
})
export class SiteToolbarComponent implements OnInit {
    @Output()
    onFilterChange: EventEmitter<any> = new EventEmitter();
    @Output()
    onCreateNew: EventEmitter<any> = new EventEmitter();

    @Output()
    onSiteCreate: EventEmitter<any> = new EventEmitter();

    filters: Filter[] = [
        // { label: 'Modifié récemment', by: 'recently_update' },
        { label: "Plus récent", by: "more_recent" },
        { label: "Plus ancien", by: "older" },
        { label: "A - Z", by: "A-To-Z" },
        { label: "Z - A", by: "Z-To-A" }
    ];
    curFilter = this.filters[0];

    @HostListener("document:click", ["$event"])
    clickedOutside($event) {
        if (this.dashBService.showSiteTemplates) {
            const nodeCpt = document.querySelector("dashboard-site-toolbar");
            if (nodeCpt) {
                const outCpt = !nodeCpt.contains($event.target);
                if (outCpt && !this.dashBService.inApercuSite) {
                    this.toggleCreation();
                } else {
                    // si il a cliquer sur le container toolbar on close quand meme
                    const isContainer = $event.target.classList.contains(
                        "we-container"
                    );
                    if (isContainer) {
                        this.toggleCreation();
                    }
                }
            }
        }
    }

    constructor(
        public dashBService: DashboardService,
        private siteApi: SitesService
    ) {}

    toggleCreation() {
        this.dashBService.showSiteTemplates = !this.dashBService
            .showSiteTemplates;
        this.dashBService.limitationSite = null;
    }

    ngOnInit() {}

    changeFilter(filter: Filter) {
        // console.log('filter', filter);
        this.curFilter = filter;
        this.onFilterChange.emit(filter);
    }
}



