import { Component, OnInit, ViewChild, OnDestroy } from "@angular/core";
import { ScriptService } from "../../../shared/services/script.service";
import { ReactCptComponent } from "../../../shared/components/react-cpt/react-cpt.component";
import { environment } from "../../../../environments/environment";

@Component({
    selector: "app-facture",
    templateUrl: "./facture.component.html",
    styleUrls: ["./facture.component.scss"]
})
export class FactureComponent implements OnInit, OnDestroy {
    public rcpt;
    public props;
    scriptsLoaded = false;

    @ViewChild("dtpaiement")
    dt: ReactCptComponent;

    constructor(private scriptService: ScriptService) {}

    getCpt(name: string) {
        if (typeof window[name] !== "undefined") {
            const reactCpt = window[name];
            this.scriptsLoaded = true;
            return reactCpt.default;
        }
    }

    ngOnInit() {
        window["__UID_SITE"] = [
            {
                nom: "paiement",
                distant_id: environment.uidPaiementWifeosite.distant_id,
                uid: environment.uidPaiementWifeosite.uid
            }
        ];

        window["__CFG_SITE"] = {
            shop: {
                commun: {
                    ribbons: [
                        {
                            type: "promo",
                            bgColor: "#f4253e",
                            txt: "Promo"
                        },
                        {
                            type: "soon",
                            bgColor: "#50A6E0",
                            txt: "Bientôt dispo"
                        },
                        {
                            type: "new",
                            bgColor: "#49D383",
                            txt: "Nouveau"
                        }
                    ],
                    id: 17,
                    tvaApplicable: false,
                    tvaRecuperable: true,
                    affichageRuptureStock: false,
                    avisAutorise: false,
                    horsStock: false,
                    ordreArticlesDefaut: 1,
                    joursNouveaute: 30,
                    modeMaintenance: false,
                    magasinActive: false,
                    devise: {
                        id: 1,
                        intitule: "Euro",
                        codeIso: "EUR",
                        sign: "€"
                    },
                    pays: {
                        id: 8,
                        codeIso: "FR",
                        nom: "France (Hors DOM TOM)",
                        indicatif: "33",
                        devise: {
                            id: 1,
                            intitule: "Euro",
                            codeIso: "EUR",
                            sign: "€"
                        }
                    },
                    noDelivery: false
                }
            },
            global: {
                logo: "",
                msg_tva: ``,
                infosLegales: `<p>Sarl BOURDET au capital de 1000 euros</p>\n<p>6 rue de l'Iseran</p>\n<p>78180 Montigny le Bretonneux</p>\n<p>Numéro d'identification : R.C.S.503 777 286 VERSAILLES</p>\n<p>Numéro de TVA intracommunautaire : FR90503777286</p>`,
                numLastFacture: 1
            }
        };

        this.scriptService.addScript({
            id: "factureCss",
            name: "factureCss",
            type: "css",
            src: "https://editor.wifeosite.com/assets/css/minty.min.css"
        });

        this.scriptService.loader("factureCss").then(() => {
            this.scriptService.loader("UtilsRcpt").then(() => {
                // DraftEditor necessaire pour les components l'utilisant dans l'editeur
                // Cela fix le bug où DraftEditor n'etait pas defini : l'user aller dans la facturation puis dans l'editeur
                this.scriptService.loader("DraftEditorRcpt").then(() => {
                    this.scriptService.loader("WifeoEngine").then(() => {
                        this.scriptService.loader("Moment").then(() => {
                            this.scriptService
                                .loader("EspaceReserveRcpt")
                                .then(() => {
                                    this.rcpt = this.getCpt(
                                        "EspaceReserveRcpt"
                                    );
                                    this.props = {
                                        wifeoCommande: true
                                        }
                                  
                                });
                        });
                    });
                });
            });
        });
    }

    ngOnDestroy(): void {
        // throw new Error("Method not implemented.");
        this.scriptService.removeScript("factureCss", false);
        window["__CFG_SITE"] = {};
        window["__UID_SITE"] = [];
    }
}



