import {
    Component,
    OnInit,
    ViewChild,
    EventEmitter,
    Output,
    HostListener,
    ElementRef
} from "@angular/core";
import { SitesService, Domain } from "../../../api";
import { environment } from "../../../../environments/environment.preprod";
import { ErrorsService } from "../../../global/services/errors.service";

declare var $: any;
@Component({
    selector: "dashboard-domain-list",
    templateUrl: "./domain-list.component.html",
    styleUrls: ["./domain-list.component.scss"]
})
export class DomainListComponent implements OnInit {
    @Output()
    connect: EventEmitter<any> = new EventEmitter();

    domains: Domain[] = [];
    isLoaded = false;

    constructor(
        private siteApi: SitesService,
        private errService: ErrorsService,
        private elRef: ElementRef
    ) {}

    ngOnInit() {
        this.getDomains();
    }

    getDomains() {
        this.siteApi.apiDomainsMeGet().subscribe(
            domains => {
                this.isLoaded = true;
                const { domain } = environment;
                this.domains = domains.filter(d => {
                    return (
                        !d.name.includes(domain) &&
                        !d.name.includes("wifeosite.com")
                    );
                });
            },
            error => {
                this.errService.handleErrorHttp(error);
                this.isLoaded = true;
            }
        );
    }

    connectDomain(domain) {
        this.connect.emit(domain);
    }

    @HostListener("document:click", ["$event"])
    clickout($event) {
        if (this.elRef.nativeElement.contains(event.target)) {
            // console.log("clicked inside");
        } else {
            $(".we-popover").wePopover("hide");
        }
    }

    onMainChange(domain) {
        // Si ancien domain principal : on le passe en secondaire
        const prevMainDomain = this.domains.find(
            d => domain.site && d.site && d.main && d.site.id === domain.site.id
        );
        if (prevMainDomain) {
            prevMainDomain.main = false;
        }

        // On update le domain qui vient d'etre maj
        const idxDomain = this.domains.findIndex(d => d.id === domain.id);
        this.domains[idxDomain] = domain;
    }
}



