import { Injectable } from "@angular/core";
import { Subject } from "rxjs/Subject";
import { Template, PublicService, Site, SitesService } from "../../api";
import { StoreService } from "../../shared/services/store.service";
import { ErrorsService } from "../../global/services/errors.service";
import { Router } from "@angular/router";

@Injectable()
export class DashboardService {
    // Observable des events qui se passe sur le dashboard
    events = new Subject<any>();

    // affiche ou non le loader quand on crée un site
    loadingCreateSite = false;
    templates: Site[] = [];
    templatesLoaded = false;

    // les sites de l'user
    sites: Site[] = [];
    sitesLoaded = false;

    showSiteTemplates = false;
    inApercuSite = false;
    limitationSite = null;

    constructor(
        private publicApi: PublicService,
        private siteApi: SitesService,
        private store: StoreService,
        private errorsService: ErrorsService,
        private router: Router
    ) {}

    laodSiteTemplates() {
        if (this.templatesLoaded) {
            return;
        }
        this.publicApi.publicTemplatesSitesGet().subscribe(e => {
            this.templates = e;
            this.templatesLoaded = true;
        });
    }

    applyOverflowHiddenOnBody() {
        document.body.style.overflow = "hidden";
    }

    resetOverflowOnBody() {
        document.body.style.overflow = "";
    }

    duplicateSite(event) {
        return new Promise((resolve, reject) => {
            if (this.loadingCreateSite) {
                return;
            }
            this.loadingCreateSite = true;
            this.siteApi.apiSitesCloneDuplicatesPost(event.id).subscribe(
                site => {
                    this.loadingCreateSite = false;
                    this.sites.unshift(site);
                    this.store.sites = this.sites;
                    this.editSite(site);
                },
                error => {
                    this.loadingCreateSite = false;
                    const msg = this.errorsService.handleErrorHttp(error);
                    this.limitationSite = msg;
                    this.router.navigateByUrl("dashboard/accueil");
                    window.scrollTo(0, 0);
                    // reject();
                }
            );
        });
    }

    editSite(site: Site) {
        //console.log("check que le site a bien ses modules d'activer");
        // this.moduleService.activateModule("files").then(() => {
        this.store.curSite = this.sites.find(_site => _site.id === site.id);
        localStorage.setItem("webSiteId", String(site.id));
        location.href="/editor.html";
        //this.router.navigateByUrl("/editor");
        // });
    }

    // Recupere les sites de l'user depuis l'api
    // stocke les sites dans un service 'store'
    getSites() {
        if (this.sitesLoaded) {
            return;
        }
        if (this.store.getSites().length === 0) {
            this.siteApi.apiSitesMeGet().subscribe(e => {
                this.store.setSites(e);
                this.sites = e;
                this.sitesLoaded = true;
            });
        } else {
            this.sites = this.store.getSites();
        }
    }

    createSite() {
        return new Promise((resolve, reject) => {
            this.loadingCreateSite = true;
            this.siteApi.apiSitesPost().subscribe(
                site => {
                    this.loadingCreateSite = false;
                    this.sites.unshift(site);
                    this.store.sites = this.sites;
                    this.editSite(site);
                },
                error => {
                    this.loadingCreateSite = false;
                    const msg = this.errorsService.handleErrorHttp(error);
                    this.limitationSite = msg;
                    window.scrollTo(0, 0);
                    // reject();
                }
            );
        });
    }

    deleteSite(site: Site) {
        return new Promise((resolve, reject) => {
            this.store.curSite = this.sites.find(_site => _site.id === site.id);
            this.siteApi.apiSitesSiteDelete(String(site.id)).subscribe(e => {
                this.store.sites = this.sites = this.sites.filter(
                    _site => _site.id !== site.id
                );
                resolve();
            });
        });
    }
}



