import {
    Component,
    AfterViewInit,
    OnInit,
    Input,
    Output,
    EventEmitter,
    ViewChild,
    ChangeDetectorRef,
    ElementRef,
    OnDestroy,
} from "@angular/core";
import { ModalService } from "../../../shared/services/modal.service";
import { ModalComponent } from "../../../shared/components/modal/modal.component";
import { environment } from "../../../../environments/environment";
import { ActivatedRoute, Router } from "@angular/router";
import { DashboardService } from "../../services/dashboard.service";
import { Site } from "../../../api";

/**
 * Page qui affiche un apercu de site de demo
 */
@Component({
    selector: "dashboard-apercu-site",
    templateUrl: "./apercu-site.component.html",
    styleUrls: ["./apercu-site.component.scss"],
})
export class ApercuSiteComponent implements OnInit, AfterViewInit, OnDestroy {
    @ViewChild("iframeContent") app_iframe: ElementRef;

    srcIframe = "";

    siteTemplate: Site;
    idxSite = 0;
    isFirstItem = false;
    isLastItem = false;

    constructor(
        private route: ActivatedRoute,
        private router: Router,
        public dashBService: DashboardService
    ) {}

    ngOnInit() {
        this.dashBService.inApercuSite = true;
        this.dashBService.applyOverflowHiddenOnBody();
        this.route.paramMap.subscribe((params) => {
            const id = +params.get("id");
            this.idxSite = this.dashBService.templates.findIndex(
                (s) => s.id === id
            );
            this.loadSite();
            this.isFirstItem = this.idxSite === 0;
            this.isLastItem =
                this.idxSite === this.dashBService.templates.length - 1;
        });
    }

    ngAfterViewInit() {}

    onLoad() {
        // this.iframeLoaded = true;
    }

    ngOnDestroy(): void {
        this.dashBService.resetOverflowOnBody();
        this.dashBService.inApercuSite = false;
    }

    prevSite() {
        this.idxSite--;
        this.changeSite();
    }

    nextSite() {
        this.idxSite++;
        this.changeSite();
    }

    loadSite() {
        this.siteTemplate = this.dashBService.templates[this.idxSite];
        if (!this.siteTemplate) {
            this.router.navigateByUrl(`dashboard/accueil`, {
                replaceUrl: true,
            });
            return;
        }
        this.srcIframe = `https://${this.siteTemplate.domains[0].name}`;
    }

    changeSite() {
        this.siteTemplate = this.dashBService.templates[this.idxSite];
        if (this.siteTemplate) {
            this.router.navigateByUrl(
                `dashboard/apercu-site/${this.siteTemplate.id}`
            );
        }
    }

    closePage($event) {
        // preventDefault pour eviter que le click soit intercepter par site-toolbar pour close la view
        $event.preventDefault();
        $event.stopPropagation();
        this.router.navigateByUrl("dashboard/accueil").then(() => {
            this.dashBService.showSiteTemplates = true;
        });
    }
}



