import { Component, OnInit, OnDestroy } from "@angular/core";
import { DashboardService } from "../../services/dashboard.service";
import { StoreService } from "../../../shared/services/store.service";
import { ScriptService } from "../../../shared/services/script.service";

/**
 * Point d'entré Dashboard
 * Charge des scripts, sites templates
 */
@Component({
    selector: "app-main",
    templateUrl: "./main.component.html",
    styleUrls: ["./main.component.scss"],
})
export class MainComponent implements OnInit, OnDestroy {
    momentLoaded: boolean = false;
    constructor(
        public storeService: StoreService,
        public dashBService: DashboardService,
        public scriptService: ScriptService
    ) {}

    ngOnInit() {
        this.scriptService.load("js", "Moment").then((data) => {
            this.scriptService.load("js", "MomentFr").then((data) => {
                this.momentLoaded = true;
            });
        });
        this.dashBService.laodSiteTemplates();
        // this.storeService.emailUser = localStorage.getItem("emailUser");
        // document.body.style.backgroundColor = "#f4f8fb";
    }

    ngOnDestroy(): void {
        // document.body.style.backgroundColor = "#ffffff";
    }
}



