import {
    Component,
    OnInit,
    Input,
    OnChanges,
    SimpleChanges
} from "@angular/core";
import { Site } from "../../../api";
import {
    state,
    trigger,
    style,
    transition,
    animate
} from "@angular/animations";
import { PermissionService } from "../../../shared/services/permission.service";

@Component({
    selector: "dashboard-site-list",
    templateUrl: "./site-list.component.html",
    styleUrls: ["./site-list.component.scss"],
    animations: [
        trigger("shrinkOut", [
            state("in", style({ height: "*" })),
            transition("* => void", [
                style({ height: "*" }),
                animate(250, style({ height: 0 }))
            ])
        ])
    ]
})
export class SiteListComponent implements OnInit, OnChanges {
    @Input() sites: Site[] = [];

    @Input() orderBy: string = "recently_update";

    order: string = "site.created";

    reverse: boolean = true;

    constructor(public permService: PermissionService) {}

    ngOnInit() {}

    ngOnChanges(changes: SimpleChanges): void {
        if (changes.orderBy) {
            this.setOrder(changes.orderBy.currentValue);
        }
    }

    setOrder(value: string) {
        if (this.order === value) {
            return;
        }

        switch (value) {
            case "recently_update":
                break;
            case "more_recent":
                this.order = "site.created";
                this.reverse = true;
                break;
            case "older":
                this.order = "site.created";
                this.reverse = false;
                break;
            case "A-To-Z":
                this.order = "site.domains.name";
                this.reverse = false;
                break;
            case "Z-To-A":
                this.order = "site.domains.name";
                this.reverse = true;
                break;
        }

        // console.log('value', value)
        // console.log('order', this.order)
        // console.log('reverse', this.reverse)
    }
}



