import {
    Component,
    OnInit,
    Input,
    AfterViewInit,
    ViewChild,
    ElementRef,
    HostListener,
    OnDestroy,
} from "@angular/core";
import { Site } from "../../../api";
import { DashboardService } from "../../services/dashboard.service";
import { ScriptService } from "../../../shared/services/script.service";
import { GetMainDomainPipe } from "../../../shared/pipes/get-main-domain.pipe";
import { PermissionService } from "../../../shared/services/permission.service";
import { environment } from "../../../../environments/environment";
declare var $: any;
declare var moment: any;
@Component({
    selector: "dashboard-site-card",
    templateUrl: "./site-card.component.html",
    styleUrls: ["./site-card.component.scss"],
})
export class SiteCardComponent implements OnInit, AfterViewInit, OnDestroy {
    @Input()
    site: Site;

    @ViewChild("tplOptions") tplOptions: ElementRef;

    btnEdit = {
        label: "Editer mon site",
        icon: { active: true, className: "fal fa-pencil-alt" },
        className: "we-btn we-btn-secondary",
    };

    btnBoost = {
        label: "Booster",
        icon: { active: true, className: this.permService.iconPlan },
        className: "we-btn we-btn-plan-free",
    };

    defaultCssOptsPanel = "we-cursor-pointer we-p-2";
    classNameOpts = "we-restrict we-list-unstyled we-small we-mb-0";

    opts_show = [
        {
            name: "",
            title: "Options",
            icon: { active: true, className: "far fa-ellipsis-v fa-lg" },
            action: "show_options_section",
            className: this.defaultCssOptsPanel + " we-py-1",
            visible: true,
        },
    ];
    opts_update = [
        // {
        //     name: "Renommer le site",
        //     icon: { active: true, className: "fas fa-long-arrow-alt-up" },
        //     action: "up_section",
        //     className: this.defaultCssOptsPanel,
        //     visible: false
        // },
        // {
        //     name: "Dupliquer le site",
        //     icon: { active: true, className: "fas fa-long-arrow-alt-down" },
        //     action: "down_section",
        //     className: this.defaultCssOptsPanel,
        //     visible: false
        // },
        // {
        //     name: "Transférer le site",
        //     icon: { active: true, className: "far fa-image" },
        //     action: "edit_fond_section",
        //     className: this.defaultCssOptsPanel,
        //     visible: true
        // },
        // {
        //     name: "Ajouter un contributeur",
        //     icon: { active: true, className: "far fa-arrows-alt-h" },
        //     action: "resize_section",
        //     className: this.defaultCssOptsPanel,
        //     visible: true
        // },
        // {
        //     name: "Paramètres",
        //     icon: { active: true, className: "far fa-cog" },
        //     action: "configure_site",
        //     className: this.defaultCssOptsPanel,
        //     visible: true
        // },
        {
            name: "Supprimer mon site",
            icon: { active: true, className: "far fa-trash-alt" },
            action: "delete_site",
            className: this.defaultCssOptsPanel,
            visible: true,
        },
        {
            name: "Mettre/Sortir de la maintenance",
            icon: { active: true, className: "fal fa-construction" },
            action: "offline_site",
            className: this.defaultCssOptsPanel,
            visible: true,
        },
    ];
    iter = 0;

    mainDomain;
    isSubDomain;

    popoverIsOpen = false;

    @HostListener("document:click", ["$event"])
    clickedOutside($event) {
        if (this.popoverIsOpen) {
            const nodePopoverBody = document.querySelector(".we-popover-body");
            if (nodePopoverBody) {
                const outPoover = !nodePopoverBody.contains($event.target);
                if (outPoover) {
                    this.closePopover();
                }
            }
        }
    }

    constructor(
        public dashBService: DashboardService,
        private scriptService: ScriptService,
        private getMainDomain: GetMainDomainPipe,
        public permService: PermissionService
    ) { }

    ngOnInit() {
        this.mainDomain = this.getMainDomain.transform(this.site.domains);
        this.isSubDomain =
            this.mainDomain.name.indexOf(`.${environment.domain}`) !== -1;
        if (this.site) {
            const isOnline = this.site.online;
            const offlineOption = this.opts_update.find(e => {
                return e.action == "offline_site"
            });
            //console.log(offlineOption,isOnline, this.site);
            if (offlineOption) {
                isOnline ? offlineOption.name = "Mettre en maintenance" : offlineOption.name = "Sortir de la maintenance";
            }
        }
    }

    /**
     * Initialisation popover bootstrap
     * affiche le menu item pour supprimer le site
     */
    ngAfterViewInit(): void {
        $(`.btn-options-card-${this.site.id}`).on("shown.bs.popover", () => {
            this.popoverIsOpen = true;
        });
        $(`.btn-options-card-${this.site.id}`).on("hidden.bs.popover", () => {
            this.popoverIsOpen = false;
        });
        $(`.btn-options-card-${this.site.id}`).wePopover({
            // trigger: "focus",
            html: true,
            template:
                '<div class="we-popover we-mt-3" role="tooltip"><h3 class="we-popover-header"></h3><div class="we-popover-body"></div></div>',
            content: this.tplOptions.nativeElement,
        });
    }

    imgError(image) {
        image.onerror = "";
        image.src = "assets/img/wifeo_banner.png";
        return true;
    }

    handleAction($event) {
        this.closePopover();
        this.dashBService.events.next({
            action: $event.action,
            params: { site: this.site },
        });
    }


    isFreeDomain(name) {
        var testUrl = 'wifeosite.com';
        if (name.indexOf(testUrl) !== -1) {
            return true;
        } else {
            return false;
        }
    }

    hasNonActivatedDomain() {
        let match = false;
        if(this.site.plan.name != "FREE"){
            if (this.site.domains && Array.isArray(this.site.domains)) {
                this.site.domains.forEach(element => {
                    //console.log("NDD :", element.name);
                    if(!this.isFreeDomain(element.name)){
                         match = true;
                    }
                });
            }
            return match;
        }else{
            return true;
        }
     
    }

    closePopover() {
        $(`.btn-options-card-${this.site.id}`).wePopover("hide");
    }

    ngOnDestroy(): void {
        $(`.btn-options-card-${this.site.id}`).wePopover("dispose");
    }
}



