import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { AccueilComponent } from "./pages/accueil/accueil.component";
import { PanierComponent } from "./components/panier/panier.component";
import { FactureComponent } from "./components/facture/facture.component";
import { MainComponent } from "./pages/main/main.component";
import { DomainsComponent } from "./pages/domains/domains.component";
import { ApercuSiteComponent } from "./pages/apercu-site/apercu-site.component";

const routes: Routes = [
    {
        path: "",
        component: MainComponent,
        children: [
            { path: "accueil", component: AccueilComponent },
            {
                path: "domaines",
                component: DomainsComponent
            },
            { path: "panier", component: PanierComponent },
            { path: "facturation", component: FactureComponent },
            { path: "apercu-site/:id", component: ApercuSiteComponent },
            { path: "**", redirectTo: "/dashboard/accueil", pathMatch: "full" }
        ]
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class DashboardRoutingModule {}



