import { NgModule } from "@angular/core";
import { CommonModule } from "@angular/common";

import { DashboardRoutingModule } from "./dashboard-routing.module";
import { AccueilComponent } from "./pages/accueil/accueil.component";
import { SiteListComponent } from "./components/site-list/site-list.component";
import { SiteToolbarComponent } from "./components/site-toolbar/site-toolbar.component";
import { SharedModule } from "../shared/shared.module";
import { ApiModule } from "../api";
import { SiteCardComponent } from "./components/site-card/site-card.component";
import { DashboardService } from "./services/dashboard.service";
import { PanierComponent } from "./components/panier/panier.component";
import { FactureComponent } from "./components/facture/facture.component";
import { MainComponent } from "./pages/main/main.component";
import { HTTP_INTERCEPTORS } from "@angular/common/http";
import { HttpEventInterceptor } from "../shared/classes/HttpEventInterceptor";
import { DomainsComponent } from "./pages/domains/domains.component";
import { MenuComponent } from "./components/menu/menu.component";
import { DomainListComponent } from "./components/domain-list/domain-list.component";
import { ThumbSitePipe } from "./pipes/thumb-site.pipe";
import { DomainCardComponent } from "./components/domain-card/domain-card.component";
import { ApercuSiteComponent } from "./pages/apercu-site/apercu-site.component";
// import { FormEditSiteComponent } from '../shared/components/form-edit-site/form-edit-site.component';

@NgModule({
    imports: [CommonModule, DashboardRoutingModule, SharedModule],
    declarations: [
        AccueilComponent,
        SiteListComponent,
        SiteToolbarComponent,
        SiteCardComponent,
        PanierComponent,
        FactureComponent,
        MainComponent,
        DomainsComponent,
        MenuComponent,
        DomainListComponent,
        ThumbSitePipe,
        DomainCardComponent,
        ApercuSiteComponent
        // FormEditSiteComponent
    ],
    providers: [
        DashboardService,
        {
            provide: HTTP_INTERCEPTORS,
            useClass: HttpEventInterceptor,
            multi: true
        }
    ]
})
export class DashboardModule {}



