import { Component, OnInit, ViewChild } from "@angular/core";
import { StoreService } from "../../../shared/services/store.service";
import { SitesService, Site, Domain, DomainType } from "../../../api";
import { ModalComponent } from "../../../shared/components/modal/modal.component";
import { Router } from "@angular/router";
import { environment } from "../../../../environments/environment";
import { encodeQueryData } from "../../../shared/utils/utils";
import { ErrorsService } from "../../../global/services/errors.service";
import { DomainListComponent } from "../../components/domain-list/domain-list.component";
import { DomainEditType } from "../../../api/model/domainEditType";
import { PermissionService } from "../../../shared/services/permission.service";
import { DashboardService } from "../../services/dashboard.service";

/**
 * Page pour la gestion des domaines
 */
@Component({
    selector: "app-domains",
    templateUrl: "./domains.component.html",
    styleUrls: ["./domains.component.scss"],
})
export class DomainsComponent implements OnInit {
    @ViewChild("domainList")
    domainList: DomainListComponent;
    @ViewChild("modal")
    modal: ModalComponent;

    actionsDomains = [
        {
            icon: "far fa-plus",
            label: "Acheter un nouveau domaine",
            link: `${environment.scheme}://${environment.hostWifeoCms}/${environment.pathToBuyDomain}`,
        },
        {
            icon: "far fa-person-carry",
            label: "Transférer un domaine sur Wifeo",
            click: this.transfer.bind(this),
        },
    ];
    showActionsAddDomain = false;

    selectedDomain: Domain = null;
    selectedSite: Site = null;
    pendingLink = false;
    constructor(
        private siteApi: SitesService,
        private storeService: StoreService,
        private router: Router,
        private errService: ErrorsService,
        public permService: PermissionService,
        public dashBService: DashboardService
    ) {}

    ngOnInit() {
        this.dashBService.getSites();
    }

    addDomain() {
        this.showActionsAddDomain = !this.showActionsAddDomain;
    }

    connect(domain?) {
        if (domain) {
            this.selectedDomain = domain;
            if (domain.site) {
                this.selectedSite = domain.site;
            } else {
                this.selectedSite = null;
            }

            this.modal.open();
        }
    }

    transfer() {
        const { scheme, domain, hostWifeoCms } = environment;
        const data = { ndd_action: "ndd_transfer" };
        const querystring = encodeQueryData(data);
        location.href = `${scheme}://${hostWifeoCms}/domaines?${querystring}`;
    }

    onHiddenModal() {
        this.selectedDomain = null;
    }

    // Selectionne le site dans la liste
    selectSite(site: Site) {
        if (
            this.permService.IsAuthorized(
                null,
                this.permService.PLAN.FREE,
                site
            )
        ) {
            this.selectedSite = site;
        } else {
            this.permService.openModalPlan(this.permService.LIMIT.SET_DOMAIN);
        }

        // open modal de confirmation ?
    }

    // API Met a jour le domain avec le site associé
    link() {
        if (this.pendingLink) {
            return;
        }
        this.pendingLink = true;

        const domainType: DomainEditType = {
            site: String(this.selectedSite.id),
            main: this.selectedDomain.main,
        };
        this.siteApi
            .apiDomainsDomainPut(domainType, String(this.selectedDomain.id))
            .subscribe((domain) => {
                // mettre a jour le listing des domaines
                const idxDomain = this.domainList.domains.findIndex(
                    (d) => d.id === domain.id
                );
                this.domainList.domains[idxDomain] = domain;

                if (domain.main) {
                    // on passe en secondaire l'ancien
                    const prevMainDomain = this.domainList.domains.find(
                        (d) =>
                            domain.site &&
                            d.site &&
                            d.main &&
                            d.site.id === domain.site.id
                    );
                    if (prevMainDomain) {
                        prevMainDomain.main = false;
                    }
                }

                this.pendingLink = false;
                this.modal.close();
            });
    }

    // Déselectionner le site dans la liste et ferme la modal
    cancelSelectedSite() {
        this.selectedSite = null;
        this.modal.close();
    }
}



