import { Pipe, PipeTransform } from "@angular/core";
import { Site } from "../../api";

@Pipe({
    name: "thumbSite"
})
export class ThumbSitePipe implements PipeTransform {
    transform(site: Site, pathIfNotFound: string): any {
        // site.thumb === "53195f944d3200d82c9ca1fad3534ac6fe48eda9.jpeg" car anciennement, les sites avais ce mauvais path d'image
        if (
            !site.thumb ||
            site.thumb === "53195f944d3200d82c9ca1fad3534ac6fe48eda9.jpeg"
        ) {
            return pathIfNotFound
                ? pathIfNotFound
                : "assets/img/wifeo_banner.png";
        } else {
            return site.thumb;
        }
    }
}



